.greetingForm {
    display: block;
    width: 500px;
    height: 300px;
    padding-left: 40px;
}

.inputAndButtonContainer {
    display: flex;
    gap:10px;
    width: 400px;
    height: 45px;
    /*background-color: #cc9c00;*/
}

.error {
    color: #cc0000;
    border-radius: 5px;
    padding-top: 5px;
}

.input {
    width: 300px;
    height: 40px;
    border-radius: 5px;
    border: 2px solid darkgray;
    padding: 10px;
    resize: none;
    overflow: hidden;
    font-size: 16px;
    outline: none;
}

.input:focus {
    border: 1px solid #000000;
}

.errorInput {
    border: 2px solid #cc0000;
    border-radius: 5px;
    width: 300px;
    height: 40px;
    font-size: 16px;
}

.button {
    margin-left: 10px;
    width: 80px;
    height: 40px;
    background-color: #007bff;
    color: #FFFFFF;
    border-radius: 5px;
    border: none;
    font: bold 14px/1.5 "Roboto", sans-serif;
}

.button:focus {
    cursor: pointer;
    color: #4ca6ff;
}

.button:active {
}

.button:disabled {
    background-color: #4ca6ff;
    opacity: 0.5;
}

.text {
    color: gray;
    padding-bottom: 10px;
}

.greeting {
    color: #212A34;
}
